import React, { useEffect } from 'react';
import PopupWithForm from "./PopupWithForm";
import { useFormWithValidation } from '../hooks/useForm';

function AddPlacePopup({ isOpen, onClose, onAddCard, isDataLoad }) {
  const { values, errors, isValid, handleChange, resetForm } = useFormWithValidation();

  function handleSubmit(e) {
    e.preventDefault();
    //* –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
    onAddCard(values);
  }

  useEffect(() => {
    resetForm();
  }, [isOpen, resetForm]);

  return (
    <PopupWithForm
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      name="newCard"
      id="addCard"
      title="–ù–æ–≤–æ–µ –º–µ—Å—Ç–æ"
      submitText={isDataLoad ? '–î–æ–±–∞–≤–ª—è–µ–º...' : '–î–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É'}
      isDisabled={!isValid}
    >
      <label className="popup__form-field">
        <input
          value={values.name || ""} onChange={handleChange}
          name="name"
          className="popup__input"
          id="popup__placeName-input"
          required
          minLength="1"
          maxLength="30"
          placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏"
          type="text"
        />
        <span
          id="popup__placeName-input-error"
          className={`${errors.name ? 'popup__input-error popup__input-error_visible' : 'popup__input-error'}`}
        >{errors.name}</span>
      </label>
      <label className="popup__form-field">
        <input
          value={values.link || ""} onChange={handleChange}
          name="link"
          className="popup__input"
          id="popup__placeLink-input"
          required
          placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É"
          type="url"
        />
        <span
          id="popup__placeLink-input-error"
          className={`${errors.link ? 'popup__input-error popup__input-error_visible' : 'popup__input-error'}`}
        >{errors.link}</span>
      </label>
    </PopupWithForm>
  )
}

export default AddPlacePopup;