import React, { useContext, useEffect } from 'react';
import PopupWithForm from "./PopupWithForm";
import CurrentUserContext from '../contexts/CurrentUserContext';
import { useFormWithValidation } from '../hooks/useForm';

function EditProfilePopup({ isOpen, onClose, onUpdateUser, isDataLoad }) {
  const { values, errors, isValid, handleChange, setValues, resetForm } = useFormWithValidation();
  const currentUser = useContext(CurrentUserContext);

  function handleSubmit(e) {
    e.preventDefault();
    //* –ü–µ—Ä–µ–¥–∞—ë–º –∑–Ω–∞—á–µ–Ω–∏—è —É–ø—Ä–∞–≤–ª—è–µ–º—ã—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤ –≤–æ –≤–Ω–µ—à–Ω–∏–π –æ–±—Ä–∞–±–æ—Ç—á–∏–∫
    onUpdateUser(values);
  }

  useEffect(() => {
    if (currentUser) {
      resetForm();
      setValues({
        name: currentUser.name,
        about: currentUser.about
      });
    }
  }, [isOpen, resetForm, currentUser, setValues]);

  return (
    <PopupWithForm
      name="profile"
      id="editProfile"
      title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –ø—Ä–æ—Ñ–∏–ª—å"
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      submitText={isDataLoad ? '–°–æ—Ö—Ä–∞–Ω—è–µ–º...' : '–°–æ—Ö—Ä–∞–Ω–∏—Ç—å –ø—Ä–æ—Ñ–∏–ª—å'}
      isDisabled={!isValid}
    >
      <label className="popup__form-field">
        <input
          value={values.name || ''} onChange={handleChange}
          name="name"
          placeholder="–ò–º—è"
          required
          maxLength="40"
          className="popup__input"
          id="popup__name-input"
          type="text"
        />
        <span
          id="popup__name-input-error"
          className={`${errors.name ? 'popup__input-error popup__input-error_visible' : 'popup__input-error'}`}
        >{errors.name}</span>
      </label>
      <label className="popup__form-field">
        <input
          value={values.about || ''} onChange={handleChange}
          name="about"
          placeholder="–ü—Ä–æ—Ñ–µ—Å—Å–∏—è"
          required
          maxLength="200"
          className="popup__input"
          id="popup__job-input"
          type="text"
        />
        <span
          id="popup__job-input-error"
          className={`${errors.about ? 'popup__input-error popup__input-error_visible' : 'popup__input-error'}`}
        >{errors.about}</span>
      </label>
    </PopupWithForm>
  )
}

export default EditProfilePopup;