import React, { useRef, useEffect } from 'react';
import PopupWithForm from "./PopupWithForm";
import { useFormWithValidation } from '../hooks/useForm';

function EditAvatarPopup({ isOpen, onClose, onUpdateAvatar, isDataLoad }) {
  const { values, errors, isValid, handleChange, resetForm } = useFormWithValidation();

  const avatarRef = useRef(); //* –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –æ–±—ä–µ–∫—Ç, –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—ã–π —Ö—É–∫–æ–º, –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é

  function handleSubmit(e) {
    e.preventDefault();

    onUpdateAvatar(values);
  }

  useEffect(() => {
    resetForm();
  }, [isOpen, resetForm]);

  return (
    <PopupWithForm
      name="updateAvatar"
      id="update"
      title="–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä"
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      submitText={isDataLoad ? '–û–±–Ω–æ–≤–ª—è–µ–º...' : '–û–±–Ω–æ–≤–∏—Ç—å'}
      isDisabled={!isValid}
    >
      <label className="popup__form-field">
        <input
          value={values.avatar || ""} onChange={handleChange}
          ref={avatarRef}
          name="avatar"
          className="popup__input"
          id="popup__avatarLink-input"
          required
          placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ"
          type="url"
        />
        <span
          id="popup__avatarLink-input-error"
          className={`${errors.avatar ? 'popup__input-error popup__input-error_visible' : 'popup__input-error'}`}
        >{errors.avatar}</span>
      </label>
    </PopupWithForm>
  )
}

export default EditAvatarPopup;